import React from 'react';
import ProductCard from './ProductCard'
import './Product.css'
import {Link} from 'react-router-dom'
import Product1_1 from '../assets/Product/Product1_1.png'
import Product2_1 from '../assets/Product/Product2_1.png'
import Product3_1 from '../assets/Product/Product3_1.png'
import Product4_1 from '../assets/Product/Product4_1.png'
import Product5_1 from '../assets/Product/Product5_1.png'
import Product6_1 from '../assets/Product/Product6_1.png'
import Product7_1 from '../assets/Product/Product7_1.png'
import Product8_1 from '../assets/Product/Product8_1.png'

const Product = () => {
    return(
        <div className="">
            <div className="product">
            <Link to="/product/1" className="linkto">
            <ProductCard photos={[Product1_1,]} price='130k' productName='Mouse Wireless'/>
            </Link>
            <Link to="/product/2" className="linkto">
            <ProductCard photos={[Product2_1,]} price='500k' productName='Headset Gaming'/>
            </Link>
            <Link to="/product/3" className="linkto">
            <ProductCard photos={[Product3_1,]} price='19JT' productName='VGA RTX'/>
            </Link>
            <Link to="/product/4" className="linkto">
            <ProductCard photos={[Product4_1,]} price='9JT' productName='Core i9'/>
            </Link>
            </div>
            <div className="product">
            <Link to="/product/5" className="linkto">
            <ProductCard photos={[Product5_1,]} price='9JT' productName='SSD M.2'/>
            </Link>
            <Link to="/product/6" className="linkto">
            <ProductCard photos={[Product6_1,]} price='400k' productName='Keyboard Wireless'/>
            </Link>
            <Link to="/product/7" className="linkto">
            <ProductCard photos={[Product7_1,]} price='12.7JT' productName='Cloud Storage'/>
            </Link>
            <Link to="/product/8" className="linkto">
            <ProductCard photos={[Product8_1,]} price='76JT' productName='Workstation'/>
            </Link>
            </div>
        </div>
    )
}

export default Product;