import React from 'react';
import ProductDetail from '../ProductDetail/ProductDetail'
import { Route } from 'react-router-dom'
import Product1_1 from '../assets/Product/Product1_1.png'
import Product1_2 from '../assets/Product/Product1_2.png'
import Product1_3 from '../assets/Product/Product1_3.png'
import Product2_1 from '../assets/Product/Product2_1.png'
import Product2_2 from '../assets/Product/Product2_2.png'
import Product2_3 from '../assets/Product/Product2_3.png'
import Product3_1 from '../assets/Product/Product3_1.png'
import Product3_2 from '../assets/Product/Product3_2.png'
import Product3_3 from '../assets/Product/Product3_3.png'
import Product4_1 from '../assets/Product/Product4_1.png'
import Product4_2 from '../assets/Product/Product4_2.png'
import Product4_3 from '../assets/Product/Product4_3.png'
import Product5_1 from '../assets/Product/Product5_1.png'
import Product5_2 from '../assets/Product/Product5_2.png'
import Product5_3 from '../assets/Product/Product5_3.png'
import Product6_1 from '../assets/Product/Product6_1.png'
import Product6_2 from '../assets/Product/Product6_2.png'
import Product6_3 from '../assets/Product/Product6_3.png'
import Product7_1 from '../assets/Product/Product7_1.png'
import Product7_2 from '../assets/Product/Product7_2.png'
import Product7_3 from '../assets/Product/Product7_3.png'
import Product8_1 from '../assets/Product/Product8_1.png'
import Product8_2 from '../assets/Product/Product8_2.png'
import Product8_3 from '../assets/Product/Product8_3.png'

const Product1 = () => <ProductDetail
                        img1={Product1_1}
                        img2={Product1_2}
                        img3={Product1_3}
                        title="Mouse Wireless"
                        price="IDR 130,000"
                        />
const Product2 = () => <ProductDetail
                        img1={Product2_1}
                        img2={Product2_2}
                        img3={Product2_3}
                        title="Headset Gaming"
                        price="IDR 500,000"
                        />
const Product3 = () => <ProductDetail
                        img1={Product3_1}
                        img2={Product3_2}
                        img3={Product3_3}
                        title="VGA RTX"
                        price="IDR 19,000,000"
                        />
const Product4 = () => <ProductDetail
                        img1={Product4_1}
                        img2={Product4_2}
                        img3={Product4_3}
                        title="Core i9"
                        price="IDR 9,000,0000"
                        />
const Product5 = () => <ProductDetail
                        img1={Product5_1}
                        img2={Product5_2}
                        img3={Product5_3}
                        title="SSD M.2"
                        price="IDR 9,000,000"
                        />
const Product6 = () => <ProductDetail
                        img1={Product6_1}
                        img2={Product6_2}
                        img3={Product6_3}
                        title="Keyboard Wireless"
                        price="IDR 400,000"
                        />
const Product7 = () => <ProductDetail
                        img1={Product7_1}
                        img2={Product7_2}
                        img3={Product7_3}
                        title="Cloud Storage"
                        price="IDR 12,700,000"
                        />
const Product8 = () => <ProductDetail
                        img1={Product8_1}
                        img2={Product8_2}
                        img3={Product8_3}
                        title="Workstation"
                        price="IDR 76,000,000"
                        />

const MyProductDetail = () => {

    return(
        <div className="">
                <Route path="/product/1" component={Product1} />
                <Route path="/product/2" component={Product2} />
                <Route path="/product/3" component={Product3} />
                <Route path="/product/4" component={Product4} />
                <Route path="/product/5" component={Product5} />
                <Route path="/product/6" component={Product6} />
                <Route path="/product/7" component={Product7} />
                <Route path="/product/8" component={Product8} />
        </div>
    )
}

export default MyProductDetail;