import React from 'react';
import'./Home.css'
import Logo from './assets/Logo.png'
import { Navbar} from 'react-bootstrap'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import FontAwesome from 'react-fontawesome'
import ReactGA from 'react-ga';
import Homepage from './Homepage/Homepage';
import MyProductDetail from './MyProductDetail/MyProductDetail';

// eslint-disable-next-line
function initializeReactGA() {
    ReactGA.initialize('UA-143580910-1');
    ReactGA.pageview('/');
}

const Home = () => {

    return(
        <div className="">
            <Router>
            <Navbar scrolling dark expand="lg" sticky="top" className="nav">
                <Link to="/">
                    <Navbar.Brand className="logo">
                    <img
                        alt=""
                        src={Logo}
                        height="30"
                        className="d-inline align-top"
                    />
                    <h5>ILMUONESHOP</h5>
                    </Navbar.Brand>
                </Link>
            </Navbar>
                <Route path="/" exact component={Homepage} />
                <MyProductDetail/>
            </Router>
            <p className="made">Made By <a href="https://www.instagram.com/nublimarzuki_/" target="blank_"><strong className="strng">Nubli Marzuki Ramadhan</strong></a> With ReactJS <br/><FontAwesome name='copyright' /> 2019 ILMUONESHOP</p>
        </div>
    )
}

export default Home;