import React from 'react';
import './ProductDetail.css'
import { Container, Col, Row, Carousel }from 'react-bootstrap'
import FontAwesome from 'react-fontawesome'
import {Link} from 'react-router-dom'

const ProductDetail = (props) => {
    return(
        <div className="mt-5">
            <Container>
                <Row>
                    <Col sm={6}>
                        <Carousel indicators={false} interval="2000" className="cor">
                        <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src={props.img1}
                            alt="First slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src={props.img2}
                            alt="Third slide"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src={props.img3}
                            alt="Third slide"
                            />
                        </Carousel.Item>
                        </Carousel>
                    </Col>
                    <Col sm={6}>
                        <Link to="/" className="linkto"><FontAwesome name='caret-left' /> Back to Product</Link>
                        <h1 className="mt-4">{props.title}</h1>
                        <h5 className="mt-4">{props.price}</h5>
                        <p className="text-justify mt-4">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Facilis eum dolor, non rerum deserunt voluptates quasi, alias, modi dignissimos blanditiis ab! Molestiae eius in repellendus! Ea illum nisi quam dolorem earum neque tempore vel quasi quidem! Nemo odio ducimus rerum animi! Qui quos repellendus atque optio laudantium, reiciendis quo dolore!</p>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

ProductDetail.defaultProps = {
    title: 'Product Title',
    price: 'IDR 0'
}

export default ProductDetail;